<template>
<div >
   <mu-appbar class="fixed" :title="videoObject.title">
     <mu-icon-button icon="arrow_back" slot="left" @click="goback()" />
   </mu-appbar>
<div style="padding-top:66px;">
 <mu-card>
   <mu-card-media >
    <div id="player" class="flowplayer" >

      
    </div>
  
  </mu-card-media>
 </mu-card>
 <mu-tabs :value="currentTab" @change="handleTabChange">
    <mu-tab value="tab1" title="信息"/>
    <mu-tab value="tab2" title="图集"/>
    <mu-tab value="tab3" title="视频"/>
  </mu-tabs>
  <div class="desc-container" v-if="currentTab === 'tab1'">
   <mu-card-title :title="videoObject.models" :subTitle="videoObject.categories"/>
      <mu-card-text>
    {{videoObject.desc}}
  </mu-card-text>
  </div>
  <div v-if="currentTab === 'tab2'"> 
   <img  :src="img.url" lazy="loading" v-for="img, index in videoObject.photos" :key="index">
  </div>
  <div v-if="currentTab === 'tab3'">
    <h2></h2>
    <p>
      
    </p>
  </div>
</div>
<mu-dialog v-if="!logined" :open="dialog" title="温馨提示">
    <h3>因用户网络环境的不同，以及当前用户数量，看片时有时候会出现缓冲加载时间较长，您可以直接点击播放减少缓冲时间，如果感觉不是很流畅，可以先尝试去看其他片子，稍后再来
    </h3>
    <mu-flat-button  label="关闭" slot="actions" primary @click="()=>{dialog=false}"/>
  </mu-dialog>
</div>
</template>
<script >
	export default {
		name:'videodetails',

    mounted(){

        this.player=document.getElementById("player")
        this.initplayer()

    },
		
		data(){
			return {
				currentTab:'tab1',
        dialog:true,
        videoplayerurl:'',
       
        player:null
			}
		},
   
    beforeRouteEnter(to,from,next)
    {
      next(vm=>{
        vm.initplayer()
      })
    },

    beforeRouteLeave(to,from,next)
    {
       this.player.innerHTML=""
       if(this.flowplayer) {
        this.flowplayer.shutdown()
      }
       next(true)
     

    },
		computed:{
       videoObject() {
        return this.$store.state.cvo
       },
       logined(){
      return this.$store.state.loginedUser
      }
    },
		methods: {
      initplayer(){
          this.videoplayerurl="http://www.dmm.com/en/service/-/html5_player/=/cid="+this.videoObject.movienumber+"/mtype=AhRVShI_/service=digital/floor=idol/mode=list/"
          
          this.flowplayer=flowplayer(this.player,{
            autoplay: true,
            poster:this.videoObject.poster,
            logo: "https://flowplayer.org/media/img/mylogo.png",
            clip:{
              sources:[{
                type:"video/mp4",
               src:this.videoObject.mp4
              }]
            }
          })
          this.flowplayer.on("error",function(e,api){
            e.preventDefault()
          })
          

          this.dialog=!this.logined
      },
      resetplayer()
      {   
          flowplayer.conf.poster=this.videoObject.poster
          this.flowplayer.load({
              sources:[{
                type:"video/mp4",
                src:this.videoObject.mp4
              }]
            })
          this.dialog=true
      
      },

			fetchData () {
				alert('hello')
			},
      handleTabChange (val) {
        this.currentTab = val
      },
      goback()
      {
        this.$router.go(-1)
      }
     

    } 
  }
</script>
<style scoped>
  
   .flex{
    display: flex;
    flex-direction: column;
    flex-wrap: nowrap;
    justify-content: flex-start;
   }
   
   .desc-container{
    height:100%;
    overflow-y: scroll;
   }
   img {
    width: 100%;
   }
   .fixed {
    position:fixed;
    top: 0;
   }
 
</style>


